body {
  background-color: #62121F;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
  monospace;
}

div.contador {
    display: grid;
    grid-gap: 0px;
    margin-left: calc(50% - 5em);
    margin-top: -5.5%;
    width: 10em;
    font-family: 'Montserrat', sans-serif;
    font-size: 300%;
    font-weight: 700;
    text-align: center;
    color: #62121F;
}

div.count_days, div.count_hours, div.count_minutes, div.count_secs {
  display: inline;
  width: 2.5em;
}
div.count_days {
  grid-column: 1;
}
div.count_hours {
  grid-column: 2;
}
div.count_minutes {
  grid-column: 3;
}
div.count_secs {
  grid-column: 4;

}

.image_02_parallax_container {
    margin-top: -6%;
    overflow: hidden;
    z-index: -10;
    position: relative;
}

.invite_img_parallax_container {
    margin-top: -20%;
}

.image_03-04_parallax_container {
    margin-top: -10%;
    overflow: hidden;
    z-index: -20;
    position: relative;
}

.image_04_parallax {
  margin-top: 5%;
  margin-bottom: 10%;
}

.recuadro_01, .recuadro_02 {
    box-shadow: 8px 8px 22px 1px #1c1c1c66;
    -webkit-backdrop-filter: blur(20px);
    backdrop-filter: blur(20px);
    position: static;
    width: -moz-fit-content;
    width: fit-content;
}

.recuadro_01 {
    margin-top: -74%;
    margin-bottom: -20%;
    margin-left: calc(25% + 17%);
}

.image_04_parallax_container {
    margin-top: -10%;
    overflow: hidden;
    z-index: -30;
    position: relative;
}

.recuadro_02 {
    margin-top: -75%;
    margin-bottom: 15%;
    margin-left: calc(25% - 17%);
}

.text_02_parallax_container {
    margin-top: -10%;
}

.image_05_parallax_container {
    margin-top: -30%;
    overflow: hidden;
    z-index: -40;
    position: relative;
}

.recuadro_03 {
    margin-top: -50%;
    margin-left: calc(25% - 0%);
}

.image_06_container {
    margin-top: -30%;
    overflow: hidden;
    z-index: -40;
    position: relative;
}

.header {
    width: 100%;
    height: 300em;
    display: flex;
    flex-direction: column;
    z-index: 100;
    justify-content: center;
    align-items: center;
    position: relative;
}

div.dark_shine {
    background: #3400086b;
    border-radius: 4em;
    margin-top: 20%;
    position: absolute;
    height: 30%;
    width: 60%;
    filter: blur(30px);
    z-index: -10;
}

h1 {
    font-family: 'Audrey', sans-serif;
